import Vue from 'vue'
import Router from 'vue-router'
import index from '@/index/index'  //
import ChangePassword from '@/ChangePassword/index' //
import EssentialInformation from '@/EssentialInformation/index'//
import MyAssets from '@/MyAssets/index'//
import ApplicationManagement from '@/ApplicationManagement/index'//
import register from '@/register/index'// 
import OrderManagement from '@/OrderManagement/index'//
import WalletManagement from '@/WalletManagement/index'//
import Agreement from '@/Agreement/index'//
import test from '@/test/index'//
import CreateApp from '@/CreateApp/index' //
import ImproveInformation from '@/ImproveInformation/index'//







Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', redirect: '/src/index/index.vue' },
    {
      path: '/src/index/index.vue',
      name: 'index',//
      component: index
    },
    {
      path:'/src/MyAssets/index.vue',
      name:'MyAssets',//
      component:MyAssets
    },
    {
    path:'/src/EssentialInformation/index.vue',
    name:'EssentialInformation',//
    component:EssentialInformation
    },
    {
      path:'/src/ChangePassword/index.vue',
      name:'ChangePassword',//
      component:ChangePassword
    },
    {
      path:'/src/ApplicationManagement/index.vue',
      name:'ApplicationManagement',//
      component:ApplicationManagement
    },
    {
      path:'/src/register/index.vue',
      name:'register',//
      component:register
    },
    {
      path:'/src/OrderManagement/index.vue',
      name:'OrderManagement',//
      component:OrderManagement
    },
    {
      path:'/src/WalletManagement/index.vue',
      name:'WalletManagement',//
      component:WalletManagement
    },
    {
     path:'/src/Agreement/index.vue',
     name:'Agreement',//
     component:Agreement
    },
    {
      path:'/src/test/index.vue',
      name:'test',//
      component:test
    },
  
    {
      path:'/src/CreateApp/index.vue',
      name:'CreateApp',//
      component:CreateApp
    },
    {
     path:'/src/ImproveInformation/index.vue',
     name:"ImproveInformation",//
     component:ImproveInformation
    },
    
    

  
  
   
   
 
  
  ]
})



