<script>
import APPHeader from "../../src/header"; //
import {
  ApplicationManagement,
  ApplicationData,
  DeleteApp,
  DeterminePayment,
  See,
  placeOrder,
  PurchaseRecord
} from "@/api/user";
export default {
  name: "index",
  data() {
    return {
      shuju: [],
      djcha: {},
      purchaseAPP: [],
      purchaseAPP3: [],
      modal2: false,
      modal3: false,
      modal4: false,
      modal7: false,
      payPwd: "",
      id: "",
      GenerateOrder: []
    };
  },
  mounted() {
    this.application();
  },
  methods: {
    application() {
      console.log("ddddd");
      let Appyx = {};
      ApplicationManagement(Appyx)
        .then(res => {
          this.shuju = res.data;
        })
        // .catch(error => {
        //   // this.$Message.error("error");  
        //   this.$Message.error('This is an error tip ');
          
        // });
    },
    //
    See(e) {
      console.log("chakan");
      this.modal2 = true;
      console.log(e);
      let cha = {
        productId: e
      };
      console.log(cha);
      See(cha).then(res => {
        this.djcha = res.data.product;
        console.log(res.data.product);
        
      });

      // id= this.e;
    },
    //
    BecomeAnAgent() {
      console.log("");
      this.modal3 = true;
    },
    //
    ToUpgrade() {
      console.log("");
      this.modal4 = true;
      let zsapp = {};
      ApplicationData(zsapp).then(res => {
        // configList 
        console.log(res.data.configList[(0, 2)]);
        this.purchaseAPP = res.data.configList;
        this.purchaseAPP3 = res.data.configList[3];
        //proxyConfigs
        console.log(res.data.proxyConfigs);
      });
      PurchaseRecord(zsapp).then(res => {
        // console.log(res.data[].buyMeal)
      });
    },
    //
    DeleteApp(d) {
      // console.log("")
      let shan = {
        id: d
      };
      console.log(shan);
      DeleteApp(shan).then(res => {
        if (res.code == 0) {
          this.modal2 = false;
          location.reload(); //
        }
      });
    },
    //
    BuyNow(OrderID) {
      // console.log("")
      let fasong = {
        applyConfigId: OrderID
      };
      placeOrder(fasong).then(res => {
        console.log("res");
        if (res.code == 0) {
          this.modal7 = true;
          this.GenerateOrder = res.data.orderId;
        }
      });
    },
    //
    Gpurchase() {
      // console.log("")
      let danhao = {
        orderId: this.GenerateOrder,
        payPwd: this.payPwd
      };
      console.log(danhao);
      DeterminePayment(danhao)
        .then(res => {
          if (res.code == 0) {
            this.modal7 = false;
            this.$Notice.success({
              title: ""
            });
            location.reload(); //
          } else {
            this.modal7 = false;
            this.$Notice.success({
              title: ""
            });
          }
        })
        .catch(error => {
          //  
          console.log(error);
        });
    }
  },
  components: {
    APPHeader //
  }
};
</script>

